                        fontSize: '16px',
                        fontWeight: 500,
                        letterSpacing: '-0.32px',
                        display: "grid", // —Ç–µ–ø–µ—Ä –º—ñ—Å—è—Ü—å —ñ —Ä—ñ–∫ —Ü–µ–Ω—Ç—Ä–æ–≤–∞–Ω—ñ
